@-webkit-keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

body { 
  background-color: #FFF7BC;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.App {
  background-color: #261C2C;
  margin: auto;
  border-radius: 10px;
  padding: 5px;
  min-height: 500px;
  color: #FFE3D8;
  max-width: 600px;
}

header {
  border-bottom: 2px solid #A4B494;
  margin: auto;
  display: flex;
  justify-content: center;
  -webkit-animation: fadeIn 5s;
          animation: fadeIn 5s;
}



h2 {
  text-align: center;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.wait{
  display: block;
  margin: auto;
  margin-top: 20px;
}

.hide{
  display: none;
}

.mensaje {
  text-align: center;
  padding: 25px;
}
form {
  text-align: center;
  padding: 25px;
  padding-bottom: 0;
}

input {
  margin: 5px;
  padding: 10px;
  border: 1px solid #A4B494;
  border-radius: 10px;
  width: 250px;
  color: #382933;

}

input:focus{
  outline: 1px solid #E0C097;
  -moz-outline-radius: 10px;
}

button {
  background-color: #dcb38a;
  margin: 5px;
  padding: 10px;
  border: 0;
  border-radius: 10px;
  color: #382933;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-weight: bold;
}

button:hover {
  background-color: #d7a061;
}
.lista-definiciones-contenedor{
 margin: 10px;
 overflow: hidden;
 border-radius: 5px;
}

h3 {
  background-color: #3E2C41;
  padding: 25px;
  padding-bottom: 5px;
  margin: 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

p.definicion {
  text-align: justify;
  line-height: 2em;
  margin: 0;
  padding: 25px;
  padding-bottom: 0;
  background-color: #5C527F;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

p.definicion.last{
  padding-bottom: 5px;
}


.icon {
  padding: 5px;
}

.icon:hover {
  background-color: #EC9E69;
}


.etimologia {
  font-size: small;
  padding: 25px;
  padding-top: 0;
  margin: 0;
  background-color: #3E2C41;
}
